@keyframes revolution-second-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes revolution-minute-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch__object {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__elements {
  position: absolute;
}

.stopwatch__elements--minute-hand {
  transform-origin: bottom;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite revolution-minute-hand;
  bottom: 50%;
  left: 48%;
}

.stopwatch__elements--second-hand {
  transform-origin: bottom;
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite revolution-second-hand;
  bottom: 50%;
  left: 48%;
  transform: translate(-50%, 50%)rotate(0);
}

.stopwatch__elements--point {
  z-index: 3;
  background-color: orange;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  top: 46.9%;
  left: 46.9%;
}

.stopwatch__elements--second-hand--speed-up {
  animation: 10s linear infinite revolution-second-hand;
}

.stopwatch__elements--minute-hand--speed-up {
  animation: 600s steps(60, end) infinite revolution-minute-hand;
}
